@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&display=swap");
* {
  outline: none;
  box-sizing: border-box;
}
html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
img {
  max-width: 100%;
}

:root {
  --body-font: "Inter", sans-serif;
  --main-purple: #4f46e5;
  --accent-purple: #5b57e9;
  --darker-purple: #1f1189;
  --accent-white: #f9fafb;
  --main-black: #111827;
  --main-white: #c7d2fe;
  --accent-gray: #6b7280;
  --border-white: #e5e7eb;
  --delay: 0s;
}
.xdd {
  transform: translateX(-55%) translateY(40px) scaleX(0.86) scaleY(1)
    skewX(0deg) skewY(8deg) translateZ(0px);
  box-shadow: rgba(24, 24, 27, 0.4) 80px -40px 80px;
  background: var(--main-black);
  padding-bottom: 1px;
  padding-right: 2px;
  z-index: 3;
}
.xdd1 {
  transform: translateX(-75%) translateY(40px) scaleX(1) scaleY(1) skewX(0deg)
    skewY(8deg) translateZ(0px);
  z-index: 3;
}
.xdd12 {
  transform: translateX(-5px) translateY(0px) scaleX(1) scaleY(1) skewX(0deg)
    skewY(8deg) translateZ(0px);
  z-index: 2;
}
.xdd2 {
  transform: translateX(-5px) translateY(0px) scaleX(0.86) scaleY(1) skewX(0deg)
    skewY(8deg) translateZ(0px);
  box-shadow: rgba(24, 24, 27, 0.4) 80px -40px 80px;
  background: var(--main-black);
  padding-bottom: 1px;
  padding-right: 2px;
  z-index: 0;
}
.xdd3 {
  transform: translateX(40%) translateY(-20px) scaleX(0.86) scaleY(1)
    skewX(0deg) skewY(8deg) translateZ(0px);
  background: var(--main-black);
  padding-bottom: 1px;
  padding-right: 2px;
  box-shadow: none;
  z-index: 1;
}
.xdd13 {
  transform: translateX(150%) translateY(0px) scaleX(1) scaleY(1) skewX(0deg)
    skewY(8deg) translateZ(0px);
  z-index: 1;
}
body {
  font-family: var(--body-font);
  color: var(--main-white);
  display: flex;
  background: var(--main-white);
  justify-content: center;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 1000vh;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

.effect-3 {
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 5px;
  overflow: hidden;
}
.effect-3:before {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: var(--main-white);
  transform: translateX(-100%);
}
.effect-3:hover:before {
  transform: translateX(0);
}

.box-shadow-purple {
  box-shadow: var(--main-purple) 0px 3px 12px 0px;
}
.text-shadow-purple {
  text-shadow: 0 0 10px var(--main-purple);
}

@keyframes rl {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
@keyframes bt {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes bt1 {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

.anim-rl {
  animation: rl 0.8s var(--delay) both;
}
.anim-bt {
  animation: bt 0.8s var(--delay) both;
}
.anim-bt1 {
  animation: bt1 0.8s var(--delay) both;
}
.box-xl {
  box-shadow: rgb(145 158 171 / 40%) -40px 40px 80px 0px;
}

::-webkit-scrollbar-track {
  border-radius: 12px;
}
::-webkit-scrollbar-thumb {
  border-radius: 12px;
}
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--main-white);
}

::-webkit-scrollbar-thumb {
  background: var(--darker-purple);
  transition: 0.3s;
}
